.pre, .next
{
    
    height: 35px;
    width: 80px;
    margin: 0px 10px 0px 0px;
    border-radius: 10px;
    background: none;
    font-weight: 500;
    font-size: 13.5px;
    border: 2px solid black;
}

.pre
{
    color: white;
    background-color: black;
}
.pre:hover
{
    color: black;
    background-color: white;
}
.next:hover
{
    color: white;
    background-color: black;
}

.p
{
    width: 40vw;
    position: fixed;
    bottom: 0;
    left: 460px;
    background-color: white;
    height: 50px;
    border-top: 1px solid black;
    padding-top: 15px;

}

.border
{
    margin: 0px;
    width: 100vw;
    border: 1px solid rgb(111, 111, 111);

}